//'use client'

let email = "";
let password = "";

// let  url = "https://localhost:44311/api/register";
// let request = new Request(url, {
//     method: "POST",
//     body: '{ "username": "email@test.com", "password": "password" }',
//     headers: {"Content-Type": "application/json; charset=utf-8"},
//     mode: "no-cors",
// });

const headers = new Headers();
headers.append("Content-Type", "text/plain");

function handleOnChange(event) {
    if (event.target.name === "email") {
        email = event.target.value;
    }
    else if (event.target.name === "password") {
        password = event.target.value;
    }
}
async function submitForm() {
    console.log(email, password);
    let  url = "https://localhost:44311/api/register";
    let request = new Request(url, {
        method: "POST",
        body: JSON.stringify({ username: "test10@test.com", password: "password" }),
        headers: {
            //"Accept": "application/json",
            "Content-Type": "application/json;charset=utf-8",
        },
        //mode: "no-cors",
    });
    const data = await fetch(request);

    console.log("data", data);
}

export default function Register() {
    return (
        <section className="flex-auto">
            <h1>Register new User</h1>
            <div>
                <p>Email</p>
                <input type="email" name="email" required={true} placeholder="email@example.com" /*onChange={handleOnChange}*/ />
                <p>Password</p>
                <input type="password" name="password" required={true} placeholder="xxxxxx" /*onChange={handleOnChange}*/ />
                <br />
                <br />
                <input type="submit" required={true} value="Register" /*onClick={submitForm}*/ />
            </div>
        </section>
    );
}
